.container {
  background-color: #070707;
  padding: 20px;
  border-radius: 20px 25px 25px 20px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 300px;
  box-shadow: 0px 0px 15px 1px inset rgba(12, 12, 12, 0.87);
  position: relative;
}
.container .title {
  color: white;
  margin-bottom: 15px;
}
.container .blockForimgAndPrice {
  display: flex;
  align-items: center;
  position: relative;
}
.container .blockForimgAndPrice .image {
  width: 200px;
  height: 150px;
}
.container .blockForimgAndPrice .price {
  background-color: rgba(37, 37, 37, 0.7);
  border-radius: 10px;
  padding: 5px 10px;
  color: #02d714;
  position: absolute;
  top: 10px;
  right: -15px;
  transform: rotate(-15deg);
  font-size: 18px;
}
.container .blockInformation {
  display: flex;
  column-gap: 15px;
  padding: 0px 0px 8px 0px;
}
.container .blockInformation span {
  color: white;
  text-shadow: 0px 1px 2px black;
}
.container .blockForBtn {
  display: flex;
  justify-content: center;
  position: relative;
  width: 100%;
}
.container .blockForBtn button {
  padding: 4px 40px;
  transition: 500ms;
  background-color: #010b52;
  color: white;
  font-weight: bold;
  border: 2px solid #131313;
  border-radius: 10px;
  letter-spacing: 1px;
}
.container .blockForBtn button:hover {
  cursor: pointer;
  scale: 0.97;
}
.container .blockForBtn .cartIcon {
  margin-left: 20px;
  font-size: 20px;
  transition: 500ms;
  position: absolute;
  right: -5px;
}
.container .blockForBtn .cartIcon:hover {
  cursor: pointer;
  scale: 1.2;
}

