.blockForMap {
  width: 40%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;

  h3 {
    font-size: 25px;
    color: white;
    margin-bottom: 20px;
    letter-spacing: 1px;
    text-shadow: 0px 1px 4px #0a0a0a;
  }

  .iframeMap {
    border-radius: 10px;
    box-shadow: 0px 0px 5px 1px #181818;
    width: 100%;
    height: 100%;
  }
}
