.container {
  display: flex;
  color: yellow;
  padding: 15px 15px;
  box-sizing: border-box;
  border-bottom: 1px solid gray;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  position: relative;
  z-index: 9;
}
.container .delete {
  position: absolute;
  right: 5px;
  top: 5px;
  background: transparent;
  border: none;
  color: white;
  font-weight: bold;
}
.container .delete:hover {
  cursor: pointer;
  scale: 1.1;
}
.container .nameAndCount {
  display: flex;
  column-gap: 15px;
  align-items: center;
}
.container .nameAndCount .name {
  color: white;
  font-weight: bold;
  font-size: 18px;
  text-shadow: 0px 1px 5px black;
}
.container .nameAndCount .count {
  color: #02d714;
  font-weight: bold;
  text-shadow: 0px 1px 5px black;
}
.container .imageCount {
  display: flex;
  align-items: flex-end;
}
.container .imageCount img {
  width: 150px;
  height: 112.5px;
}

