.container {
  padding: 50px 0px;
  background-color: #000000;
  flex-grow: 20;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  position: relative;
}
.container .block {
  width: 500px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  position: relative;
  align-items: center;
}
.container .block .name {
  color: white;
}
.container .block .img {
  height: 250px;
}
.container .block .spanContent {
  color: white;
  width: 400px;
  line-height: 1.2;
  font-size: 22px;
  text-shadow: 0px 1px 1px black;
}
.container .block .spanContent .greenSpan {
  color: green;
  font-size: 20px;
}
.container .block button {
  padding: 5px 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 10px;
  border-radius: 20px;
  margin-top: 15px;
  color: #0702b0;
  font-size: 18px;
  text-shadow: 0px 1px 1px black;
  background-color: #ffffff;
  transition: 700ms;
}
.container .block button .iconCart {
  width: 35px;
}
.container .block button:hover {
  cursor: pointer;
  background-color: #eef4fa;
}

